import React from 'react';
import { useStoreContext } from "../../utils/GlobalState";
import { REMOVE_FROM_CART, UPDATE_CART_QUANTITY } from "../../utils/actions";
import { idbPromise } from "../../utils/helpers";

const CartItem = ({ rent }) => {

  const [, dispatch] = useStoreContext();

  const removeFromCart = rent => {
    dispatch({
      type: REMOVE_FROM_CART,
      _id: item._id
    });
    idbPromise('cart', 'delete', { ...rent });

  };

  const onChange = (e) => {
    const value = e.target.value;
    if (value === '0') {
      dispatch({
        type: REMOVE_FROM_CART,
        _id: item._id
      });
      idbPromise('cart', 'delete', { ...rent });

    } else {
      dispatch({
        type: UPDATE_CART_QUANTITY,
        _id: item._id,
        purchaseQuantity: parseInt(value)
      });
      idbPromise('cart', 'put', { ...rent, rentQuantity: parseInt(value) });

    }
  }

  return (
    <div className="flex-row">
      <div>
    
      </div>
      <div>
        <div>{rent.name}, ${rent.quantity}</div>
        <div>
          <span>Qty:</span>
          <input
            type="number"
            placeholder="1"
            value={item.rentQuantity}
            onChange={onChange}
          />
          <span
            // role="img"
            aria-label="trash"
            onClick={() => removeFromCart(rent)}
          >
            üóëÔ∏è
          </span>
        </div>
      </div>
    </div>
  );
}

export default CartItem;
